.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #826afb;
}

form {
  max-width: 600px;
  width: 100%;
  background: #fff;
  padding: 25px 25px 30px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  color: #333;
  margin: 25px;
  display: flex;
  flex-direction: column; /* Ensure fields are stacked vertically */
}

form h2 {
  font-size: 30px;
  text-align: center;
}

.input-box {
  margin-top: 20px;
  display: flex;
  flex-direction: column; /* Ensure the fields stack vertically */
}

.input-box .field {
  width: 100%;
  height: 50px;
  background: transparent;
  border: 2px solid #ddd;
  outline: none;
  border-radius: 6px;
  padding: 12px 15px; /* Adjust padding for better field size */
  font-size: 16px;
  color: #333;
  margin-top: 8px;
  box-sizing: border-box; /* Include padding in width/height calculation */
}

.input-box .field.mess {
  height: 200px;
  resize: none;
}

form button {
  width: 100%;
  height: 55px;
  background: #826afb;
  border: none;
  border-radius: 6px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  font-weight: 500;
  margin-top: 20px;
  transition: 0.5s;
}

form button:hover {
  background: #624ec6;
}
