@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap'); /* Importing a modern font */

.home {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Poppins', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; 
}

.headerContainer {
  width: auto;
  margin-left: 50px;
}

.headerContainer h1 {
  justify-content: center;
  align-items: center;
  font-size: 90px;
  height: 60px;
  font-weight: 600; 
  color: white;
}

.headerContainer p {
  padding: 0;
  font-size: 40px;
  font-weight: 300; 
  color: white;
}

.headerContainer h1:hover,
.headerContainer p:hover {
  color: white;
}

.headerContainer button {
  color: black;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  justify-content: center;
  background-color: #121619;
  height: 50px;
  width: 180px;
  color: white;
  text-decoration: none;
  font-size: 20px;
  transition: transform 0.3s ease, background-color 0.3s ease;
  font-family: 'Poppins', sans-serif; /* Consistent font for buttons */
}

.headerContainer button:hover {
  background-color: #07090a;
  transform: scale(1.1); /* Enlarges the button slightly on hover */
}

@media only screen and (max-width: 650px) {
  .home {
    justify-content: center;
    align-items: center;
  }

  .headerContainer {
    margin-left: 0px;
    border-radius: 10px;
    padding: 10px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #121619;
    color: white;
  }

  .headerContainer h1 {
    font-size: 40px;
    height: 30px;
    color: white;
    font-weight: 600;
  }

  .headerContainer p {
    font-size: 30px;
    color: white;
    font-weight: 300;
  }

  .headerContainer button {
    background-color: white;
    color: #121619;
    margin-bottom: 30px;
    transition: transform 0.3s ease, background-color 0.3s ease;
    font-family: 'Poppins', sans-serif;
  }

  .headerContainer button:hover {
    background-color: rgb(225, 225, 225);
    color: #121619;
    transform: scale(1.1);
  }
}
